@font-face {
    font-family: "Wix";
    src: url("./fonts//Wix_Madefor_Display/static/WixMadeforDisplay-Bold.ttf");
}

@font-face {
    font-family: "Oxygen";
    src: url("./fonts/Oxygen/Oxygen-Regular.ttf");
}

*{
    box-sizing: border-box;
}

body{
    margin: 0;
    padding: 0;
    padding-bottom: 18px;
}

.error{
    color: red;
    font-weight: bold;
    font-size: 18px;
    text-align: center;
    margin: 0;
}

.hide{
    display: none;
}

.dashboard{
    padding-left: 10%;
    display: flex;
    flex-direction: column;
}

.user-page{
    width: 80%;
    margin-left: 10%;
}

body{
    background-color: rgb(0, 255, 217);
    display: flex;
    justify-content: center;
    font-family: "wix", sans-serif;
}

.login-page,
.sign-up-page{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100vh;
    width: 80vw;
}

h1{
    font-size: 72px;
    border-bottom: 5px solid purple;
    animation: 3s underline infinite alternate;
}

.intro{
    font-size: 32px;
    animation: fadein 1s ease-in;
}

.login form,
.sign-up form{
    display: flex;
    flex-direction: column;
    gap: 20px;
    font-size: 28px;
    background-color: white;
    padding: 18px;
    border: 3px solid black;
    font-family: "Oxygen", sans-serif;
    margin-left: 20px;
}

input{
    height: 28px;
    font-size: 18px;
    margin-left: 18px;
}

.form-item{
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-weight: bold;
    font-family: ""Oxygen, sans-serif;
}

button{
    background-color: rgb(182, 171, 171);
    font-size: 18px;
    padding: 6px;
    cursor: pointer;
    transition: all 0.5s ease-out;
    margin-top: 12px;
}

button:hover{
    background-color: black;
    color: white;
}

a{
    color: purple;
    font-weight: bold;
    transition: all 0.2s ease-in;
}

a:hover{
    color: blue;
}

.card{
    display: flex;
}

.card-header{
    display: flex;
    justify-content: center;
    margin-top: 0;
    padding: 0;
}

.card{
    border: none;
}

.or{
    text-align: center;
    font-size: 18px;
    margin: 0;
}

header{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    width: 80vw;
    max-width: 100%;
    height: 100px;
    margin-top: 32px;
    border-bottom: 3px solid white;
    position: sticky;
    top: 0;
    background-color: rgb(0, 255, 217);
    padding-top: 8px;
    z-index: 10;
}

.header-gap{
    height: 18px;
    position: sticky;
    top: 100px;
    background-color: rgb(0, 255, 217);
    z-index: 5
}

header h2{
    margin-top: 20px;
    cursor: pointer;
}

header h2:hover{
    color: blue;
}

h2::selection,
img::selection,
.option::selection{
    background: none;
}

.user{
    width: 150px;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    align-items: center;
    z-index: 10
}

.user-pic{
    border-radius: 48px;
    height: 72px;
    width: 72px;
    border: 3px solid black;
}

.option{
    background-color: white;
    color: black;
    padding: 18px;
    font-size: 18px;
    cursor: pointer;
    width: 150px;
    margin-left: auto;
}

.option:hover{
    background-color: black;
    color: white;
}

.posts{
    padding-left: 24px;
    width: 80%;
}

.post{
    background-color: white;
    padding: 10px 24px;
    width: 100%;
    margin-top: 24px;
    border-bottom: 2px solid black;
    border-right: 2px solid black;
}

.post-header{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.post-user{
    display: flex;
    gap: 24px;
    align-items: center;
    font-size: 24px;
    cursor: pointer;
}

.post-user img{
    height: 72px;
    width: 72px;
    border-radius: 48px;
}

.post-footer{
    display: flex;
    justify-content: space-evenly;
}

.footer-item{
    display: flex;
    gap: 12px;
    align-items: flex-start;
    font-size: 22px;
    border-right: 2px solid grey;
    border-left: 2px solid grey;
    padding: 0 18px;
}

.footer-item:hover{
    background-color: rgb(225, 225, 225);
    cursor: pointer;
}

.post-footer img{
    height: 32px;
}

.content{
    font-size: 28px;
    margin-top: 12px;
    font-family: "Oxygen";
    border-bottom: 1px solid rgb(210, 210, 210);
}


.post-form{
    margin-top: 10px;
    width: 100%;
    display: flex;
}

.post-form input{
    height: 64px;
    width: 100%;
    font-family: "Oxygen";
    border-radius: 8px;
    overflow: auto;
    padding-left: 10px;
}

.post-form-div{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 80%;
}

.post-btn{
    background-color: blue;
    color: white;
    border:  none;
    padding: 10px 24px;
    border-radius: 12px;
    font-size: 24px;
}

.post-user:hover{
    color: blue;
}

.user-page-header{
    display: grid;
    grid-template-columns: 1fr 3fr;
    grid-template-rows: 48px 1fr;
    margin-top: 18px;
    padding-bottom: 18px;
    border-bottom: 3px solid white;
}

.user-page-header img{
    grid-column: 1;
    grid-row: 1 / 3;
    border: 3px solid black;
    margin-right: 24px;
    height: 240px;
}

.user-page-header h2{
    font-size: 48px;
}

.bio{
    margin-top: 24px;
    font-size: 24px;
}

.bio textarea{
    margin-top: 12px;
    width: 100%;
    height: 128px;
    font-size: 18px;
}

.bio .confirm-edit-bio{
    margin-left: 0;
}

.bio-top{
    display: flex;
    align-items: center;
}


.user-page-bottom{
    display: flex;
    justify-content: flex-start;
    gap: 10%;
    max-width: 100%;
}

.user-page-bottom .posts{
    width: 100%;
}

.user-page-bottom .friends{
    background-color: yellow;
    border-radius: 18px;
    height: fit-content;
    padding: 20px;
    margin-top: 18px;
    max-width: 500px;
    border: 3px solid black;
}

.user-item{
    display: flex;
    margin-top: 18px;
    align-items: center;
    gap: 18px;
    background-color: white;
    padding: 10px 18px;
    border-radius: 12px;
}

.user-item h2{
    font-size: 20px;
}

.user-item img{
    height: 72px;
    width: 72px;
    border-radius: 48px;
}

.user-item:hover{
    background-color: rgb(182, 171, 171);
    color: blue;
    cursor: pointer;
}

.user-item button,
.user-page-header button{
    margin: 0;
    margin-left: auto;
    background-color: yellow;
    border-radius: 12px;
}

.user-item button:hover,
.user-page-header button:hover{
    background-color: rgb(255, 119, 0);
}

.user-item .request-sent,
.user-page-header .request-sent{
    background-color: orange;
}

.user-item .request-sent:hover,
.user-page-header .request-sent:hover{
    background-color: orangered;
}

.user-item .friends,
.user-page-header .friends{
    background-color: green;
}

.user-item .friends:hover,
.user-page-header .friends:hover{
    background-color: rgb(0, 66, 0);
}

.user-item .accept-request,
.user-page-header .accept-request{
    background-color: skyblue;
}
.accept-request:hover,
.user-page-header .accept-request:hover{
    background-color: rgb(47, 115, 142);
}

.notifications{
    height: 50px;
    margin-left: auto;
    margin-top: 10px;
    cursor: pointer;
}

.notifications p{
    position:  relative;
    bottom: 60px;
    left: 40px;
    background-color: red;
    color: wheat;
    border-radius: 24px;
    width: 35px;
    text-align: center;
    font-weight: bold;
    font-size: 22px;
}

.notification{
    background-color: white;
    display: flex;
    padding: 10px 24px;
    align-items: center;
    border-radius: 12px;
    margin-top: 18px;
}

.notification img{
    height: 64px;
    width: 64px;
    border-radius: 32px;
    margin-right: 18px;
    cursor: pointer;
}

.notification .accept-request{
    margin: 0;
    margin-left: auto;
    margin-right: 18px;
    background-color: green;
    color: white;
}

.notification button:hover{
    background-color: greenyellow;
}

.notifications-page h2{
    margin-top: 24px;
}

.notification .reject-request{
    background-color: red;
    color: white;
    margin: 0;
}

.notification .reject-request:hover{
    background-color: rgb(136, 2, 2);
}

.user-page-header button{
    width: 350px;
    height: 80px;
    margin-right:  auto;
    margin-left: 0;
    margin-top: 18px;
}

.bio button{
    margin-left: 24px;
    background-color: blue;
    color: white;
    width: 300px;
    height: 64px;
}

.bio button:hover{
    background-color: black;
}

.bio-top button{
    margin-top: 0;
    margin-bottom: 10px;
    width: 120px;
    height: 64px;
}

.friend{
    display: flex;
    font-size: 18px;
    gap: 8px;
    margin-top: 18px;
    align-items: center;
    cursor: pointer;
}

.friend:hover{
    color: blue;
}

.friend img{
    height: 64px;
    border-radius: 24px;
    width: 64px;
    
}

.comment{
    display: flex;
    flex-direction: column;
    border-top: 1px solid grey;
    padding: 5px;
    font-family: "Oxygen", sans-serif;
}

.comment-header{
    display: flex;
    gap: 32px;
    align-items: center;
    cursor: pointer;
    font-family: "Wix", sans-serif;
    justify-content: space-between;
}

.comment-user{
    display: flex;
    gap: 24px;
    align-items: center;
}

.comment-footer{
    display: flex;
    justify-content: center;
    gap: 32px;
    margin-top: 18px;
    font-family: "Wix", sans-serif;
}

.comment-footer .footer-item{
    font-size: 18px;
}

.comment-footer img{
    height: 24px;
}

.comment-header img{
    height: 48px;
    width: 48px;
    border-radius: 24px;
}

.comment-header:hover{
    color: blue;
}

.comment-date{
    margin-left: auto;
}

.comment-header:hover .comment-date{
    cursor: auto;
    color: black;
}

.comments{
    margin-top: 18px;
}

.comment-form{
    margin-bottom: 18px;
    display: flex;
    align-items: center;
}

.comment-form input{
    padding: 18px;
    margin-right: 12px;
    width: 80%;
    font-family: "Origin", sans-serif;
}

.comment-form button{
    background-color: blue;
    color: white;
    margin: 0;
}

.comment-form button:hover{
    background-color: rgb(168, 168, 255);
}

.other{
    margin-top: 24px;
}

.loading{
    align-self: center;
    margin-right: 20%;
}

.loading-div{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.loading-div .loading{
    margin: 0;
}

.file-upload img{
    height: 40px;
}

.file-upload{
    display: flex;
    align-items: center;
    font-size: 18px;
    gap: 12px;
    cursor: pointer;
}

.post-img{
    height: 220px;
    margin-bottom: 18px;
}

.file-location{
    font-size: 18px;
    font-family: "Oxygen";
}

.user-page-header .upload{
    border: none;
}

.user-page-header .confirm-profile-pic{
    height: 48px;
    max-width: 180px;
    padding: 0px;
    margin: 0;
}

.guest-login{
    background-color: orange;
}

.guest-login:hover{
    background-color: yellow;
    color: black;
}

@keyframes underline {
    0%{
        border-bottom: 5px solid purple;
    }
    50%{
        border-bottom: 5px solid rgb(255, 0, 255);
        color: purple;
    }
    100%{
        padding-bottom: 5px solid purple;
    }
}

@keyframes fadein {
    0%{
        opacity: 0;
    }

    100%{
        opacity: 1;
    }
}

@media (max-width: 1100px){
    h1{
        font-size: 64px;
    }

    .user-page .friends h3{
        font-size: 18px;
    }

    .user-page-bottom{
        gap: 10px;
    }

    .login-page,
    .sign-up-page{
        width: 100vw;
        padding: 48px;
    }

}

@media (max-width: 800px){
    .user-page-bottom{
        flex-direction: column;
        align-items: center;
    }

    .user-page-header img{
        max-width: 280px;
    }

    .user-page-header button{
        width: 180px;
    }

    .user-page-header h2{
        font-size: 32px;
    }

    .notifications-page{
        padding: 12px;
        width: 90vw;
    }

}

@media (max-width: 500px) {
    .login-page,
    .sign-up-page{
        margin-top: 50%;
        padding: 18px;
    }

    .dashboard{
        width: 100vw;
        padding: 0;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .posts{
        margin-top: 18px;
        margin-bottom: 18px;
    }

    .posts h2{
        font-size: 24px;
    }

    .post-header img{
        width: 32px;
        height: 32px;
    }

    .post-footer{
        width: 100%;
    }

    .footer-item{
        font-size: 12px;
        padding: 4px;
    }

    .footer-item img{
        height: 18px;
    }

    .comments h3{
        font-size: 18px;
    }

    .post-img{
        max-width: 100%;
    }

    header{
        width: 100vw;
    }
    .user-item{
        margin-left: 10px;
        margin-right: 10px;
    }

    .user-page{
        width: 100vw;
        margin: 0;
    }

    .user-page-header{
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .user-page .posts{
        padding: 0 5px;
    }


}